var _self;
import navbar from '@/components/navbar/navbar.vue'

export default {
	components: {
		navbar
	},
	data() {
		return {
			accountId: "", //账号id
			merchantId: "", //商家id
			adminInfo: {}, //商户管理员信息获取对象
		}
	},
	/**
	 * 生命周期函数--监听页面加载
	 */
	onLoad: function(options) {
		_self = this;
		uni.getStorage({
			key: 'useinfo',
			success: function(res) {
				// console.log(res.data);
				_self.merchantId = res.data.merchant.merchantId
				_self.accountId = res.data.id
				// console.log(_self.merchantId);
				_self.getadminInfo();
			}
		});
	},
	methods: {
		getadminInfo() {
			var _param = {
				merchantId: this.merchantId, //商家id
				accountId: this.accountId, //	账号id
			};
			// console.log(this.merchantId)		
			this.$util.get(`/traceability_am/login/adminInfo?merchantId=${_param.merchantId}&accountId=${_param.accountId}`).then(
				res => {
					console.log(res)
					if (Number(res.data.code) === 200) {
						this.adminInfo = res.data.data;
						//   uni.showToast({
						//         title: res.data.message,
						//         icon: 'none',
						//         mask: true,
						//         duration: 1000,
						//  });

					} else {
						uni.showToast({
							title: res.data.message,
							icon: 'none',
							mask: true,
							duration: 1000,
						})
					}
				});

		},

		// 点击图片预览
		handlePreview(url) {
			uni.previewImage({
				current: url,
				urls: [url],
				indicator: 'default'
			})
		}

	}

}
